<template>
  <div>
    <template>
      <portal to="app-header">
        <!-- TODO -->
        <!-- <v-btn class="mr-2">
          添加图片
        </v-btn> -->
        <a
          class="mr-2"
          href="javascript:location.reload()"
        >Reload the page</a>
      </portal>
    </template>
    <template>
      <v-stage
        ref="stage"
        :config="stageSize"
        style="border: 1px solid;"
        @mousemove="handleStageMouseMove"
        @mouseout="handleStageMouseOut"
        @click="handleStageClick"
      >
        <v-layer>
          <v-text :config="{ draggable: true,text: '里面', y: 10, fontSize: 20, fill: 'purple'}" />
          <v-text :config="{ draggable: true,text: mytext, x: 100, y: 10, fontSize: 20, fill: 'tomato'}" />
          <v-line :config="{ draggable: true, x: 100, y: 100, points: [0, 0, 150, -30], stroke: 'green' }" />
          <v-circle :config="{ draggable: true, x: 100, y: 100, radius: 50, fill: 'tomato' }" />
          <v-rect :config="{ draggable: true, x: 100, y: 100, width: 100, height: 50, stroke: 'black' }" />
          <v-image :config="{ draggable: true, x: 200, y: 100, image: image, }" />
          <!-- TODO transformer -->
          <v-transformer ref="transformer" />
        </v-layer>
      </v-stage>
      <div>
        外面
        <!-- <img
          src="./cake.jpg"
          alt="Image"
        > -->
      </div>
    </template>
  </div>
</template>

<script>
const width = window.innerWidth;
const height = window.innerHeight;

const image = new window.Image();
image.src = require("./cake.jpg");

export default {
  data() {
    return {
      stageSize: {
        width: width,
        height: height - 100
      },

      mytext: '233',
      image: image,
    };
  },

  methods: {
    writeMessage(message) {
      this.mytext = message;
    },
    handleStageMouseOut() {
      this.writeMessage('Mouseout Stage');
    },
    handleStageMouseMove() {
      const mousePos = this.$refs.stage.getNode().getPointerPosition();
      const x = mousePos.x;
      const y = mousePos.y;
      this.writeMessage('x: ' + x + ', y: ' + y);
    },
    handleStageClick() {
      this.writeMessage('Click Stage');
    },
  }
};
</script>

